body {
  text-align: center;
  background: #79f279;
  font-family: Arial, Helvetica, sans-serif;
}

button {
  width: 200px;
  height: 40px;
  color: #000;
  text-transform: uppercase;
  border: 2px solid #9294f8;
  border-radius: 5px;
  margin-top: 3rem;
  margin-left: auto;
  font-size: 1rem;
  text-decoration: none;
  position: relative;
  overflow: hidden;
}

button:hover {
  box-shadow: 1px 1px 25px 10px rgba(146, 148, 248, .4);
}

input {
  text-align: center;
  color: #000;
  text-transform: uppercase;
  border: 2px solid #9294f8;
  border-radius: 5px;
  margin-top: 3rem;
  padding: .5rem;
  font-size: 1rem;
}

form {
  color: #f5f5f5;
  justify-content: center;
  justify-items: end;
  display: grid;
}

